* {
  font-family: "Segoe UI";
  margin: 0px;
  padding: 0px;
}

body {
  background-color: lightgray;
}

main {
  width: 50%;
  margin: 0px auto;
}

.player-main-container {
  position: sticky;
  position: -webkit-sticky;
  display: flex;
  top: 0;
  background-color: white;
  box-shadow: 0px 1px 2px 1px gray;
  margin: 10px 0px;
  align-items: center;
  padding: 5px;
}

.player-container {
  width: 100%;
  margin: 0px 5px;
}

.song-poster {
  margin: 0px 5px;
}

.song-details {
  text-align: center;
  flex-grow: 1;
}

.nav-buttons {
  display: flex;
  margin: 10px 0px 0px;
  justify-content: center;
}

.nav-buttons li {
  transition: 0.3s;
  list-style: none;
  margin: 0px 5px;
  padding: 10px;
  width: 64px;
  cursor: pointer;
}

.nav-buttons li:hover {
  transition: 0.3s;
  background-color: lightgray;
}

.duration {
  display: flex;
}

.song-list-container {
  width: 60%;
  margin: 10px auto;
}

.status {
  display: flex;
  align-items: center;
}

.song-item {
  transition: 0.2s;
  display: flex;
  margin: 8px 0px;
  padding: 5px 10px;
  box-shadow: 0px 1px 2px 1px gray;
  background-color: white;
  color: black;
  cursor: pointer;
}

.song-not-playing {
  opacity: 0;
}

.song-playing {
  opacity: 1;
}

.song-item:hover {
  transition: 0.2s;
  box-shadow: 0px 1px 10px 1px gray;
}

.song-item:active{
  box-shadow: none;
}

.song-item img,
.song-item-playing img {
  margin: 0px 10px;
}

.song-item-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#songDur {
  appearance: none;
  outline: none;
  height: 10px;
  background-color: rgb(46, 101, 173);
  margin: 5px 10px;
  width: 75%;
  cursor: pointer;
  border-radius: 5px;
}

#songDur::-webkit-slider-thumb {
  appearance: none;
  height: 18px;
  width: 18px;
  border-radius: 9px;
  border: 2px solid black;
  background-color: white;
}

#songDur::-webkit-slider-thumb:hover {
  height: 22px;
  width: 22px;
  border-radius: 11px;
}

@media (min-width: 320px) and (max-width: 640px) {
  main {
    width: 100%;
  }

  .nav-buttons li {
    width: 33%;
  }

  .song-list-container {
    width: 90%;
  }

  .song-poster img{
    display: none;
  }

  .song-item img:nth-child(2){
    height: 96px;
    width: 96px;
  }
}

@media (min-width: 650px) and (max-width: 850px) {
  main {
    width: 75%;
  }

  .nav-buttons li {
    width: 32px;
  }
}
